.allPage {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.titleBar {
  background-color: #065f27; /* Blue color */
  text-align: center;
  width: 100vw;
}

.title {
  margin: 0;
  color: #fff;
}

.underPage {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  padding: 20px;
}

.sidebar {
  width: 20%;
  background-color: #f4f4f4;
  padding: 20px;
  height: 100%;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
  justify-content: center;
}

.sidebar ul li {
  cursor: pointer;
  margin-bottom: 10px;
}

.content {
  width: 80%;
  padding: 20px;
  height: 100%;
}